.button2 {
  position: relative;
  background-color: rgb(99, 6, 128);
  border: 2px solid rgb(183, 8, 236);
  margin: 30px;
  font-size: 30px;
  color: #ffffff;
  padding: 30px 30px;
  width: 200px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  border-radius: 50%;
}

.button3 {
  position: relative;
  background-color: rgb(99, 6, 128);
  border: 2px solid rgb(183, 8, 236);
  margin-right: 100px;
  margin-left: 30px;
  font-size: 30px;
  color: #ffffff;
  padding: 30px 30px;
  width: 200px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  border-radius: 50%;
}

.button4 {
  position: relative;
  background-color: rgb(99, 6, 128);
  border: 2px solid rgb(183, 8, 236);
  margin-right: 70px;
  margin-left: 30px;
  font-size: 30px;
  color: #ffffff;
  padding: 30px 30px;
  width: 200px;
  text-align: center;
