}

.button5 {
  position: relative;
  background-color: #ce171a;
  border: 2px solid #ffffff;
  border-color: transparent;
  margin: 0 30px;
  font-size: 30px;
  color: #ffffff;
  width: 170px;
  height: 170px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  border-radius: 50%;
}

.button2:hover {
  background-color: #ef6c00;
  color: white;
}

.button3:hover {
  background-color: #ef6c00;
  color: white;
}

.button4:hover {
  background-color: #ef6c00;
  color: white;
}

.button5:hover {
  background-color: #ef6c00;
  color: white;
}

.button2:after {
  content: "";
  background: #f1f1f1;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px !important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s;
}

.button2:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s;
}

